*{box-sizing:border-box}body{font-size:14px;color:#fff;font-family:'Roboto-Light', sans-serif;background-color:#f1f3f6}.wrapper{margin:auto;width:100%;max-width:1164px;padding:0 20px}.info--text{font-size:34px;line-height:42px;letter-spacing:1.4px;font-family:'Roboto-Light', sans-serif}.description{color:#000;font-size:12px;line-height:1.2}.section-top-block{background:url("../images/bg.jpg");background-repeat:no-repeat;background-position:top center;background-size:cover;padding-bottom:120px}.section-top-block .info--text{margin:50px 0 10px}.logo{font-size:16px;letter-spacing:1.2px;font-family:'Roboto-Bold', sans-serif;margin-right:60px}.logo .logo-text{color:#fff;text-decoration:none}.info-btn{cursor:pointer;display:flex;align-items:center;justify-content:center;height:40px;width:150px;background:#fff;color:#27122e;border:none;text-transform:uppercase;margin:18px 0 40px;font-family:'Roboto-Black', sans-serif;font-size:13px;letter-spacing:0.4px;transition:all 0.3s;position:relative;overflow:hidden;z-index:1}.info-btn:before,.info-btn:after{content:'';position:absolute;background:#72189c;z-index:-1}.info-btn:after{height:100%;left:-35%;top:0;transform:skew(50deg);transition-duration:0.6s;transform-origin:top left;width:0;transition:all 0.4s}.info-btn:hover:after{height:100%;width:135%}.info-btn:hover{color:#fff}.info-btn:focus{outline:none}.section-platforms{margin-left:calc((100% - 1124px)/2);background-color:#fff}.section-platforms__wrap{display:flex;justify-content:space-between;padding:60px 40px;margin:-120px 0 0;max-width:1190px}.section-platforms__wrap .platform{color:#000;padding:0 30px;margin-right:40px}.section-platforms__wrap .platform__title{font-size:15px;padding:10px 0;font-family:'Roboto-Black', sans-serif;letter-spacing:0.9px;margin-bottom:16px}.section-platforms__wrap .platform__description{font-size:12px;line-height:16px;font-family:'Roboto-Medium', sans-serif;letter-spacing:-0.2px}.section-platforms__wrap .platform__icon{margin-top:48px}.section-bg-lines{background:#f1f3f6;margin-left:calc((100% - 1164px)/2);padding:0 20px}.section-bg-lines__add-lines{background:linear-gradient(to left, transparent calc(100% - 2px), #e6e4ea 0);background-size:calc((100% - 5*1px)/8 + 5px) 100%}.section-bg-lines__info{display:flex;align-items:flex-start;padding:86px 70px;width:100%}.section-bg-lines--left{width:37%}.section-bg-lines--left .info--text{font-family:'Roboto-Bold', sans-serif;color:#000}.section-bg-lines--right{width:50%}.section-bg-lines--right .description{font-family:'Roboto-Medium', sans-serif;font-size:13px;line-height:24px;letter-spacing:-0.1px;margin:0}.section-platforms{margin-left:calc((100% - 1124px)/2);background-color:#fff}@media only screen and (max-width: 1024px){.section-platforms{margin-left:auto}.section-platforms__wrap .platform{color:#000;padding:0 10px;margin-right:10px}.section-bg-lines{margin-left:auto}.section-bg-lines .section-bg-lines__info{padding:86px 20px}}@media only screen and (max-width: 768px){.section-top-block{padding-bottom:10px}.section-platforms__wrap{padding:20px 40px;margin:0;max-width:1190px;flex-wrap:wrap}.section-platforms__wrap .platform{display:flex;flex-wrap:wrap;text-align:center;justify-content:center}.section-platforms__wrap .platform__icon{margin-top:28px;order:1;width:100%}.section-platforms__wrap .platform__title{order:2}.section-platforms__wrap .platform__description{order:3}.section-bg-lines__info{flex-wrap:wrap}.section-bg-lines__info .section-bg-lines--left{width:98%}.section-bg-lines__info .section-bg-lines--left .info--text{margin-bottom:20px}.section-bg-lines__info .section-bg-lines--right{width:98%}}@media only screen and (max-width: 480px){.info--text{font-size:26px}.info--text br{display:none}}
